import React, { useState } from 'react';
import { Input, Checkbox } from 'antd';
import { requiredRule } from '../../../utils/validateRules';
import CrudPage from '../../../components/common/CrudPage';
import { searchStudentAccount } from '../../../services/studentPortal';
import {
  massCreateStudentAccount,
  updatePassword,
  updateStatus,
} from '../../../services/userPortal';

export default () => {
  const [studentsSelected, setStudentsSelected] = useState({});
  const model = [
    { field: 'code', name: 'MSSV', formItem: <Input /> },
    { field: 'fullName', name: 'Họ tên', formItem: <Input /> },
    { field: 'userName', name: 'Tài khoản', formItem: <Input /> },
    { field: 'userDisabled', name: 'Vô hiệu', editFormItem: <Checkbox /> },
    { field: 'password', name: 'Mật khẩu', rules: [requiredRule], newFormItem: <Input.Password /> },
    { field: 'updatePassword', name: 'Đặt lại mật khẩu', editFormItem: <Input.Password /> },
  ];

  const config = {
    title: 'Tài khoản Sinh Viên',
    serviceGetList: searchStudentAccount,
    serviceCreate: massCreateStudentAccount,
    serviceUpdate: async values => {
      console.log(values);
      if (values.updatePassword) {
        await updatePassword();
      }
      await updateStatus();
    },
    searchFormItems: ['code', 'fullName'],
    tableColumns: [
      {
        title: '',
        key: 'select',
        render: (text, record) => {
          if (record.userName) {
            return null;
          }
          return (
            <Checkbox
              onChange={e => {
                if (e.target.checked) {
                  setStudentsSelected({ ...studentsSelected, [record.id]: record });
                } else {
                  const newStudentsSelected = { ...studentsSelected };
                  delete studentsSelected[record.id];
                  setStudentsSelected(newStudentsSelected);
                }
              }}
            />
          );
        },
      },
      'code',
      'fullName',
      'userName',
      {
        title: 'Vô hiệu',
        key: 'userDisabled',
        dataIndex: 'userDisabled',
        render: text => {
          return <Checkbox checked={!!text} />;
        },
      },
    ],
    btnAdd: {
      props: {
        disabled: Object.keys(studentsSelected).length === 0,
      },
    },
    btnEdit: record => {
      return {
        props: {
          disabled: !record.userName,
        },
      };
    },
    saveFormItems: ['password', 'userDisabled', 'updatePassword'],
    saveFormCustomValue: values => {
      if (values.FORM_MODE === 'NEW') {
        return Object.values(studentsSelected)
          .filter(item => item)
          .map(item => {
            return {
              studentId: item.id,
              code: item.code,
              password: values.password,
            };
          });
      }
      return values;
    },
    saveFormOnSuccess: () => {
      setStudentsSelected({});
    },
  };
  return <CrudPage model={model} config={config} />;
};



