export const searchStudentAccount = ({
  fullName,
  code,
  studentIds,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Student/SearchStudentAccount`,
    method: 'POST',
    params: {},
    data: { fullName, code, studentIds, page, pageSize, orderBy, isPagingEnabled },
  });



